\n\t\t\t\t——2017年10月25日，在十九届中共中央政治局常委同中外记者见面时的讲话\n\t\t\t\t"),_c('br'),_vm._v(" "),_c('br'),_vm._v(" \n\t\t\t\t行百里者半九十，越到紧要关头，越要坚定必胜的信念，越要有一鼓作气攻城拔寨的决心。只要各地区各部门切实担起责任、真抓实干，只要贫困地区广大干部群众继续奋发进取、埋头苦干，只要全党全国各族人民万众一心、咬定目标加油干，就一定能如期打赢脱贫攻坚这场硬仗。\n\t\t\t\t"),_c('br'),_vm._v(" "),_c('br'),_vm._v(" \n\t\t\t\t——2018年10月，对脱贫攻坚工作作出的指示 \n\t\t\t\t"),_c('br'),_vm._v(" "),_c('br'),_vm._v(" \n\t\t\t\t咬定目标不放松 \n\t\t\t\t"),_c('br'),_vm._v(" "),_c('br'),_vm._v(" "),_c('img',{attrs:{"src":require("../../assets/articleImg/contentPic3.jpeg")}}),_vm._v(" "),_c('br'),_vm._v(" "),_c('br'),_vm._v(" \n\t\t\t\t贵州毕节市境内贵黔高速一段高架桥路段（2016年7月16日摄）。 \n\t\t\t\t"),_c('br'),_vm._v(" "),_c('br'),_vm._v(" \n\t\t\t\t“十三五”期间脱贫攻坚的目标是，到2020年稳定实现农村贫困人口不愁吃、不愁穿，农村贫困人口义务教育、基本医疗、住房安全有保障；同时实现贫困地区农民人均可支配收入增长幅度高于全国平均水平、基本公共服务主要领域指标接近全国平均水平。\n\t\t\t\t"),_c('br'),_vm._v(" "),_c('br'),_vm._v(" \n\t\t\t\t让人民过上好日子，是我们一切工作的出发点和落脚点。我们将坚持在发展中保障和改善民生，不断满足人民日益增长的美好生活需要，不断促进社会公平正义，使人民获得感、幸福感、安全感更加完善、更有保障、更可持续。我们将持续推进精准扶贫、精准脱贫，实现到2020年我国现行标准下农村贫困人口脱贫的目标。全面建成小康社会，13亿多中国人，一个都不能少！\n\t\t\t\t"),_c('br'),_vm._v(" "),_c('br'),_vm._v(" \n\t\t\t\t——2017年11月10日，在亚太经合组织工商领导人峰会上的主旨演讲\n\t\t\t\t"),_c('br'),_vm._v(" "),_c('br'),_vm._v(" \n\t\t\t\t全面建成小康社会最艰巨最繁重的任务在贫困地区，特别是在深度贫困地区，无论这块硬骨头有多硬都必须啃下，无论这场攻坚战有多难打都必须打赢，全面小康路上不能忘记每一个民族、每一个家庭。\n\t\t\t\t"),_c('br'),_vm._v(" "),_c('br'),_vm._v(" \n\t\t\t\t——2018年2月11日，在四川看望慰问各族干部群众时的讲话\n\t\t\t\t"),_c('br'),_vm._v(" "),_c('br'),_vm._v("\n\t\t\t\t在脱贫标准上，既不能脱离实际、拔高标准、吊高胃口，也不能虚假脱贫、降低标准、影响成色。要把握脱贫攻坚正确方向，确保目标不变、靶心不散，聚力解决绝对贫困问题，加大对非贫困县、贫困村内贫困人口的支持，严格执行贫困县退出标准和程序，确保脱贫成果经得起历史检验。\n\t\t\t\t"),_c('br'),_vm._v(" "),_c('br'),_vm._v(" \n\t\t\t\t——2019年3月7日，在参加十三届全国人大二次会议甘肃代表团审议时的讲话\n\t\t\t\t"),_c('br'),_vm._v(" "),_c('br'),_vm._v(" \n\t\t\t\t中国共产党的初心就是为人民谋幸福、为民族谋复兴，党中央想的就是千方百计让老百姓都能过上好日子。芝麻开花节节高，今后的日子会更美好。\n\t\t\t\t"),_c('br'),_vm._v(" "),_c('br'),_vm._v(" \n\t\t\t\t——2019年5月20日，在江西考察时的讲话 \n\t\t\t\t"),_c('br'),_vm._v(" "),_c('br'),_vm._v(" \n\t\t\t\t整治问题不手软 \n\t\t\t\t"),_c('br'),_vm._v(" "),_c('br'),_vm._v(" "),_c('img',{att